export default {
    props:{
        content:{
            type:String,
            default:'确定'
        },
       bgColor:{
            type:String,
            default:'#00C6FF'
       }
    },
  computed:{
        bg(){
            return `background-color:${this.bgColor}`
        }
  },
  methods:{
        buttonClick(){
            this.$emit('buttonClick');
        }
  }
}



