import {mapGetters, mapMutations} from 'vuex';
import Number  from 'base/number/number';
import Alert from 'base/alert/alert';
import {setListMixin} from 'common/js/mixin';
import {changeShopNumber} from 'common/js/util';
export default {
  data(){
    return {
      content: '您确定清空购物车吗？',
      cancelBtnText: '取消',
      alert: {
        state: true,
        text: '您确定从购物车中移除该商品吗？',
        cancelBtnText: '取消'
      },
      showFlag: false
    }
  },
  components: {
    Number,
    Alert
  },
  mixins:[setListMixin],
  props: {
    list: {
      type: Array,
      default: []
    }
  },
  methods: {
    confirm(){
      this.setShopList([]);
      this.setList([]);
    },
    clearShop(){
      this.$refs.alert.show();
    },
    coverHide(){
      this.$emit('coverHide');
    },
    show(){
      this.showFlag = true;
    },
    hide(){
      this.showFlag = false;
    },
    numChange(number, id){
      let shopList=changeShopNumber(this.shopList,number,id);
      this.setShopList(shopList);
      this.setList(shopList,this.$route.name);
    },
    price(price){
      return `¥ ${price}`
    },
    ...mapMutations({
      setShopList: 'SET_SHOP_LIST'
    })
  },
  computed: {
    ...mapGetters([
      'shopList'
    ])
  }
}



