import Alert from 'base/alert/alert';
export default {
  data(){
    return {
      num: 0
    }
  },
  components: {
    Alert
  },
  props: {
    cancelBtnText:{
     type:String,
      default:''
    },
    alertText:{
      type:String,
      default:''
    },
    alertShow: {
      type: Boolean,
      default: false
    },
    number: {
      type: Number,
      default: 0
    },
    id: {
      type: String,
      default: ''
    }
  },
  created(){
    this.num = this.number;
  },
  methods: {
    confirm(){
      this.num--;
      this.$emit('numberChange', this.num, this.id)
    },
    add(){
      this.num++;
      this.$emit('numberChange', this.num, this.id);
    },
    reduce(){
      if (this.num === 1&&this.alertShow) {
        this.$refs.alert.show();
          return ;
      }
      this.num--;
      this.$emit('numberChange', this.num, this.id)
    }
  }
}



